@import "../Common/styles/sass/mixins";
.header{
    position: fixed;
    width: 100%;
    z-index: 5;
    opacity: 0.6;
    @include flex(row,flex-end, center, nowrap);
    height: 80px;
    background-color: #212529;
    @media screen and (max-width: 920px) {
        @include flex(row, center, center, nowrap);
    }
}